* {
  margin: 0;
  padding: 0;
}

.page {
  scroll-behavior: smooth;
  background-color: #f2f6fa;
  min-width: 320px;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  height: 100vh;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

